  .page{
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    background: #EAEBEC;
    >main{
      flex-grow: 1;
    }
    >main{
      // min-width: 1024px;
      // max-width: 1440px;
      
      margin-top: 16px;
      margin-bottom: 16px;
      display: flex;
      justify-content: space-between;
      padding: 0 16px;
      width: 100%;
      align-self: center;
      }
    }

  #resumeEditor{
    min-width: 35%;
    background: #444;
  }
  #resumePreview{
    /*width: 61.66667%;*/
    flex: 1;
    margin-left: 16px;
    background: #777;
  }
  svg.icon{
    height: 1em;
    width: 1em;
    fill:currentColor;
    vertical-align: -0.1em;
    font-size: 16px;
  }
